import React, {useContext, useEffect, useState} from "react";
import {useCookies} from 'react-cookie';
import ProductContext from "../Context/ProductContext";
import {Link} from 'react-router-dom';
import ScrollTopAction from "../Action/ScrollTopAction";
import Swal from "sweetalert2";
import CartCard from "./CartCard";


function CartTable(props){
  let {state, action} = useContext(ProductContext);
  let [cookie, setCookie, removeCookie] = useCookies(['cart']);
  let [data, setDate] = useState(cookie.cart);
  useEffect(()=>{
    setDate(cookie.cart);
  }, [cookie.cart]);
  let i = 0;
  let count = (id, color, size, count, max)=>{
    setCookie('cart', [...cookie.cart.map(item=>{
      if(item.id == id && item.color == color && item.size == size && count <= max && count > 0){
        item.count = count;
        return item;
      }else{
         return item;
      }
    })], {path: '/'})
  }
  let removeItem = (id, color, size)=>{
    setCookie('cart', [...cookie.cart.filter(item=>item.color != color || item.id != id || item.size != size)], {path: '/'});
    if(cookie.cart.length == 1){
      setCookie('cart', [],{path: '/', expires: (new Date())});
      removeCookie('cart');
    }
    Swal.fire({
      position: 'center',
      icon: 'error',
      title: 'Product removed from cart',
      showConfirmButton: false,
      timer: 1500
    })
  }
  let table = null;
  if(cookie.cart){
    table = data.map((item, key)=>{
      let data = state.product.filter(pro=>pro.id == item.id)[0];
      i++;
      return (
        <div key={key} >
        <CartCard item={item} data={data} counter={i} removeItem={removeItem} count={count} />
      </div>
      )
    })
  }
    return (
        <>
        {cookie.cart ?
      (
       <div className="w-full min-h-[350px]">
       {table}
       </div>
    )  
      :
      <div className="w-full h-full flex justify-center items-center min-h-[470px] lg:min-h-[520px]">
        <h1 className="text-center block w-full text-gray-500 text-2xl py-8 h-full">Cart is empty!</h1>
      </div>
      }
        </>
    )
}


export default CartTable;