import React, {useContext} from "react";
import {useCookies} from 'react-cookie';
import ProductContext from "../Context/ProductContext";


function CartMoreDetail(props){
  let {state, action} = useContext(ProductContext);
  let [cookie, setCookie] = useCookies(['cart']);
  let totalprice = 0;
  let productcount = 0;
  let price = null;
  if(cookie.cart){
    price = cookie.cart.forEach((item, valuee)=>{
    let data = state.product.find(pro=>item.id == pro.id);
    totalprice += (data.price * item.count);
    productcount += item.count;
  });
  }
    return (
        <>
        {cookie.cart ? 
        (
          <div className="mt-2 lg:mt-4">
            <div className="w-full text-center bg-gradient-to-r from-gray-700 via-gray-600 to-gray-700 mt-2 rounded py-2 px-4 text-white">
            <table className="w-full">
            <thead>
              <tr className="border-b-[1px] border-b-white">
              <th className="py-2 px-4 w-1/2">Total Items</th>
              <th className="py-2 px-4 w-1/2">Total Price</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="py-2 px-4 w-1/2 text-xl">{productcount}</td>
                <td className="py-2 px-4 w-1/2 text-xl">{Math.round(totalprice)}$</td>
              </tr>
            </tbody>
            </table>
          </div>
          </div>
    //       <div className="flex flex-col lg:flex-row justify-between pt-5">
    //    <div className="flex bg-gray-600 justify-center border-[1px] border-gray-200 shadow-sm  text-white text-center w-full mb-2 lg:mb-0 lg:w-[49%] rounded text-xl py-2">
    //      <span>
    //      Product Count  :  {productcount}
    //      </span>
    //    </div>
    //    <div className="flex bg-gray-600 justify-around border-[1px] border-gray-200 shadow-sm  text-white text-center  w-full lg:w-[49%] rounded text-xl py-2">
    //      <span>
    //      Total Price  :  {Math.round(totalprice)} $
    //      </span>
    //    </div>
    //  </div>
        )
      :
      null
      }
        </>
    )
}

export default CartMoreDetail;