import React from "react";

function CartCardLoading(props){
    return(
        
            <div className={`w-full mb-3 border-[1px] border-gray-200 shadow-sm rounded h-[500px] lg:h-[112px] md:h-[367.86px]  relative overflow-hidden anim-right ${props.status ? "hidden" : "block"}`}>
            
            <h6 className="bg-gray-200 p-2 rounded-full absolute top-4 right-4 text-white w-14 h-9 lg:hidden"></h6>
            <div className="absolute bottom-5 left-1 right-1 mx-auto p-2 h-16 rounded bg-gray-200 w-[90%] text-white text-center">
                
            </div>
            </div>
        
    )
}

export default CartCardLoading;
