import React, {useEffect} from "react";
import CartMain from "./Cart/CartMain";
import CartTopBanner from "./Cart/CartTopBanner";

function CartPage(props){
  useEffect(()=>{
    document.title = "HM Shop | Cart";
},[])
    return(
        <div className="container mx-auto px-2 xl:px-0 flex flex-col">
          <CartTopBanner />
          <CartMain />
  </div>
    )
}

export default CartPage;