import React from "react";
import {useCookies} from 'react-cookie';
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import ScrollTopAction from "../Action/ScrollTopAction";

function CartButtonOption(props){
    let nav = useNavigate();
    let [cookie, setCookie, removeCookie] = useCookies(['cart']);
    let removeAll = ()=>{
        setCookie('cart', [],{path: '/', expires: (new Date())});
        removeCookie('cart');
        Swal.fire({
            position: 'center',
            icon: 'error',
            title: 'Cart removed',
            showConfirmButton: false,
            timer: 1500
          })
    }
    let next = ()=>{
        ScrollTopAction();
        nav('/checkout');
    }
    return (
        <>
        {cookie.cart && cookie.cart.length>-1?
        (<div className="flex justify-between w-full mt-4 rounded">
        <button className="bg-red-500 hover:bg-red-600 transition-all duration-200 text-white py-2 px-4 rounded w-[49%]" onClick={removeAll}>Delete All</button>
        <button onClick={next} className="bg-green-500 hover:bg-green-600 transition-all duration-200 text-white py-2 px-4 rounded w-[49%]">CheckOut</button>
      </div>)
    :
    null  
    }
        </>
    )
}

export default CartButtonOption;