import React, {useState} from "react";
import {Link} from 'react-router-dom';
import ScrollTopAction from "../Action/ScrollTopAction";
import CartCardLoading from "./CartCardLoading";

function CartCard(props){
  let [load, setLoad] = useState(false);
    return (
        <>
        <CartCardLoading status={load} />

        
        <div className={`border-0 lg:border-[1px] border-gray-200 shadow-sm rounded w-full flex mb-4 lg:mb-2 flex-col lg:flex-row relative ${load ? "block" : 'hidden'}`}>
        <div className="text-white flex items-center text-lg px-8 absolute left-7 top-2 -translate-x-[50%]  lg:relative lg:top-0 lg:left-0 lg:-translate-x-0">
          <div className="bg-gray-700/80 backdrop-blur text-white w-10 h-10 flex justify-center items-center rounded-full">{props.counter}</div>
        </div>
        <div className="text-white flex items-center text-lg px-0 absolute right-2 top-2 md:bottom-[-306px] md:left-[-27px] md:hidden">
          <h6 className="bg-gray-700/80 backdrop-blur p-2 rounded-full w-fit text-white">${props.data.price}</h6>
        </div>
        <div className="flex flex-col md:flex-row justify-center w-full">
          

          <div className="w-full md:w-1/2 lg:w-[175px] h-[500px] md:h-full ">
          <Link onClick={ScrollTopAction} to={`/product/${props.item.id}`} className="w-full flex justify-center p-0 lg:px-10 lg:py-2 h-full lg:h-28 ">
            <img src={`./img/${props.data.img}`} onLoad={()=>{setLoad(true)}} alt="" className="h-full object-cover object-top w-full rounded md:rounded-r-none lg:rounded-sm"/>
          </Link>
          </div>

         
          <div className="w-full flex flex-col lg:flex-row justify-center items-center p-2 md:p-0 absolute md:relative bottom-0 left-0 bg-gray-700/80 md:bg-gray-600 lg:bg-white backdrop-blur rounded-tr-none rounded-b md:rounded-b-none md:rounded-r">
            <div className="w-full lg:w-1/5 h-full md:h-1/4 lg:h-full flex justify-center items-center">
              <Link onClick={ScrollTopAction} to={`/product/${props.item.id}`} className="text-white lg:text-black text-lg md:text-xl lg:text-lg overflow-hidden text-ellipsis whitespace-nowrap m-4 lg:m-0 w-[400px] text-center">{props.data.title}</Link>
            </div>
            <div className="w-full lg:w-3/5 h-full md:h-1/4 lg:h-full flex flex-row md:flex-row justify-center items-center">
              
              <div className="w-3/6 flex flex-row justify-around items-center mb-2 md:mb-0">
                <div className="w-1/2 flex flex-col justify-center items-center">
                  <div className={`w-[40px] h-[40px] rounded-full bg-[${props.item.color}] m-2`}></div>
                </div>
                <div className="w-1/2 flex flex-row justify-center items-center">
                <span className="text-2xl text-white lg:text-black">{props.item.size.toUpperCase()}</span>
                </div>
              </div>
              <div className="w-3/6 flex flex-col justify-center items-center mb-2 md:mb-0">
                <div className="flex justify-center items-center">
                  <span className="flex rounded-full border-2 border-gray-300 p-2 text-2xl hover:border-gray-400 transition-all duration-200  w-[40px] h-[40px] justify-center items-center" onClick={()=>{
                props.count(props.item.id, props.item.color, props.item.size, props.item.count - 1, props.data.count);
              }}>
                    <span className="material-icons text-white lg:text-black">
                      remove
                      </span>
                  </span>
                <label className="text-2xl mx-4 w-6 text-center text-white lg:text-black">{props.item.count}</label>
                <span className="flex rounded-full border-2 border-gray-300 p-2 text-2xl  hover:border-gray-400 transition-all duration-200 w-[40px] h-[40px] justify-center items-center" onClick={()=>{
                props.count(props.item.id, props.item.color, props.item.size,  props.item.count + 1, props.data.count);
              }}>
                  <span className="material-icons text-white lg:text-black">
                    add
                    </span>
                </span>
                </div>
              </div>
            </div>
            <div className="w-full lg:w-1/5 h-full md:h-1/4 lg:h-full flex flex-col justify-center items-center">
              <button className="bg-red-500 hover:bg-red-600 text-white py-2 px-4 rounded transition-all duration-200 w-full md:w-2/3 lg:w-fit" onClick={(e)=>{props.removeItem(props.item.id, props.item.color, props.item.size)}}>Delete</button>
            </div>
          </div>
        </div>
          </div>
          
        </>
    )
}
export default CartCard;